import React, { forwardRef } from "react";

import { StyledDropDownContent, StyledDropDownMenuPortal, StyledItemWrapper } from "./DropDown.styles";
import { DropDownMenuProps } from "./DropDown.types";

const DropDownMenu = forwardRef<HTMLDivElement, DropDownMenuProps>(({
	children,
	dataQA,
	side = "bottom",
	align = "start",
	sideOffset,
	ariaLabel,
	$menuWidth = "",
	$menuHeight = "",
	$zIndex,
	...props
}, ref) => (
	<StyledDropDownMenuPortal
		$menuWidth={$menuWidth}
	>
		<StyledDropDownContent
			ref={ref}
			data-qa={dataQA}
			side={side}
			align={align}
			sideOffset={sideOffset}
			aria-label={ariaLabel}
			$menuWidth={$menuWidth}
			$menuHeight={$menuHeight}
			$zIndex={$zIndex}
			{...props}
		>
			<StyledItemWrapper
				$menuHeight={$menuHeight}
			>
				{children}
			</StyledItemWrapper>
		</StyledDropDownContent>
	</StyledDropDownMenuPortal>
));

DropDownMenu.displayName = "DropDownMenu";

export default DropDownMenu;
